import styles from './Diet.module.css'
import { Swiper, SwiperSlide } from 'swiper/react';
import { useState } from 'react';
import 'swiper/css';
import 'swiper/css/navigation';

import { Navigation } from 'swiper/modules';


export function Diet() {

    const [isOpen,setIsOpen] = useState(false);

    return (
        <div id='diet' className={styles['diet__container']}>
            <h2 className={styles['diet__container-title']}>РАЦИОН КОНСТРУКТОР </h2>
            <div className={styles['diet__container-content']}>
                <div className={styles['diet__container-plate']}>
                    <img src="/images/plate_1.png" alt="" />
                </div>
                <div className={styles['diet__container-text']}>
                    <p className={styles['diet__container-paragraph']}>{`Готовые варианты завтраков, обедов, ужинов и перекусов.
Вы сами выбираете, что вам съесть.
Стройность и легкость в каждой тарелке.`}</p>
                    <Swiper navigation={true} modules={[Navigation]} className={styles['diet__container-swiper']}>
                        <SwiperSlide><img src="/images/slide_1.png" alt="" /></SwiperSlide>
                        <SwiperSlide><img src="/images/slide_2.png" alt="" /></SwiperSlide>
                        <SwiperSlide><img src="/images/slide_3.png" alt="" /></SwiperSlide>
                        <SwiperSlide><img src="/images/slide_4.png" alt="" /></SwiperSlide>
                        <SwiperSlide><img src="/images/slide_5.png" alt="" /></SwiperSlide>
                        <SwiperSlide><img src="/images/slide_6.png" alt="" /></SwiperSlide>
                        <SwiperSlide><img src="/images/slide_7.png" alt="" /></SwiperSlide>
                        <SwiperSlide><img src="/images/slide_8.png" alt="" /></SwiperSlide>
                    </Swiper>
                </div>
                <div className={styles['diet__container-plate']}>
                    <img src="/images/plate_2.png" alt="" />
                </div>
            </div>
            <div className={styles['diet__container-subcontent']}>
                <p className={styles['diet__container-paragraph']}>{`Только “чистый” сбалансированный рацион с подсчитанным КБЖУ
и рецептами приготовления блюд`}</p>
                <div onClick={isOpen ? () => setIsOpen(false) : () => setIsOpen(true)} className={styles['diet__container-detail']}>ПОДРОБНЕЕ ПРО ПИТАНИЕ <svg width="21" height="18" viewBox="0 0 21 18" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M20.0503 1.11441C20.0503 0.59743 19.5757 0.178338 18.9902 0.178338L9.44946 0.178339C8.86399 0.178339 8.38938 0.597431 8.38938 1.11441C8.38938 1.63138 8.864 2.05047 9.44946 2.05047L17.9301 2.05047L17.9301 9.53901C17.9301 10.056 18.4047 10.4751 18.9902 10.4751C19.5757 10.4751 20.0503 10.056 20.0503 9.53901L20.0503 1.11441ZM1 17L1.74959 17.6619L19.7398 1.77631L18.9902 1.11441L18.2406 0.452506L0.250408 16.3381L1 17Z" fill="currentColor"/></svg></div>
                <div className={` ${styles['diet__container-detailModal']} ${isOpen ? styles.isopen : ""}`}>
                    <ul className={styles['diet__container-list']}>
                        <li>
                            <img src="/icons/check.svg" alt="" />{`Доступ к рациону-конструктору
для всех.`}
                        </li>
                        <li>
                            <img src="/icons/check.svg" alt="" />{`Простые рецепты 
из обычных продуктов`}
                        </li>
                        <li>
                            <img src="/icons/check.svg" alt="" />{`Универсальные блюда, которые Вы
сможете подстроить под себя.`}
                        </li>
                        <li>
                            <img src="/icons/check.svg" alt="" />{`Рецепты, которые подойдут 
для всей семьи, необязательно
готовить отдельно.`}
                        </li>
                        <li>
                            <img src="/icons/check.svg" alt="" />{`Вам больше не придется ломать
голову: что съесть, чтобы было
полезно и вкусно.`}
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    )
}