import styles from './Footer.module.css'

export function Footer() {
    return (
        <div className={styles['footer__container']}>
            <a href="#header"><h2 className={styles['footer__container-title']}>JULIIAZHIVAEVA</h2></a>
            <nav className={styles['footer__container-nav']}>
                <h3 className={styles['footer__container-navTitle']}>НАВИГАЦИЯ</h3>
                <a href='#training' className={styles['footer__container-navBtn']}>ТРЕНИРОВКИ</a>
                <a href='#diet' className={styles['footer__container-navBtn']}>РАЦИОН</a>
                <a href='#questions' className={styles['footer__container-navBtn']}>ВОПРОС-ОТВЕТ</a>
            </nav>
            <div id='contacts' className={styles['footer__container-contacts']}>
                <h3 className={styles['footer__container-contactsTitle']}>КОНТАКТЫ</h3>
                <div className={styles['footer__container-contactsIcons']}>
                    <a href='/' className={styles['footer__container-contactsBtn']}><img className={styles['footer__container-contactsIcon']} src="/icons/telegram.svg" alt="" /></a>
                    <a href='/' className={styles['footer__container-contactsBtn']}><img className={styles['footer__container-contactsIcon']} src="/icons/whatsapp.svg" alt="" /></a>
                    <a href='/' className={styles['footer__container-contactsBtn']}><img className={styles['footer__container-contactsIcon']} src="/icons/instagram.svg" alt="" /></a>
                </div>
            </div>
            <button className={styles['footer__container-policy']}>Политика конфиденциальности</button>
        </div>
    )
}