import styles from './Purpose.module.css'
import { useInView } from 'react-intersection-observer';

export function Purpose () {
    const { ref, inView } = useInView({
      threshold: 0.4,
      triggerOnce: true,
    });

    return (
        <div ref={ref} className={`${styles['purpose__container']} ${inView ? styles.visible : ""}`}>
            <p className={styles['purpose__container-p1']}>Мои тренировки - это...</p>
            <h2 className={styles['purpose__container-h']}>
                НЕ ПРОСТО ЦИФРА НА ВЕСАХ, <br />
                А <span>УМЕНЬШЕНИЕ</span> ОБЪЕМОВ, <span>ПРИЛИВ</span> ЭНЕРГИИ <br />
                И <span>ФОРМИРОВАНИЕ</span> ЗДОРОВЫХ ПРИВЫЧЕК, <br />
                КОТОРЫЕ ОСТАНУТСЯ С ВАМИ НАВСЕГДА
            </h2>
            <p className={styles['purpose__container-p2']}>Наша цель - не просто быстрое похудение, <br />а перезагрузка организма</p>
        </div>
    )
}