import styles from './Result.module.css'

import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import 'swiper/css/navigation';

import { Navigation } from 'swiper/modules';

export function Result() {

    return (
        <div className={styles['container__result']}>
            <h2 className={styles['container__result-title']}>РЕЗУЛЬТАТЫ</h2>
            <Swiper navigation={true} centeredSlides={true} modules={[Navigation]} className={styles['container__result-swiper']}>
                <SwiperSlide><img className={styles['container__result-img']} src="/images/result_1.webp" alt="" /></SwiperSlide>
                <SwiperSlide><img className={styles['container__result-img']} src="/images/result_2.webp" alt="" /></SwiperSlide>
                <SwiperSlide><img className={styles['container__result-img']} src="/images/result_3.webp" alt="" /></SwiperSlide>
                <SwiperSlide><img className={styles['container__result-img']} src="/images/result_4.webp" alt="" /></SwiperSlide>
            </Swiper>
        </div>
    )
}