import styles from './Questions.module.css'
import { QuestElement } from "./QuestElement"
export function Qestions() {


    return (
        <div id='questions' className={styles['questions__container']}>
            <h2 className={styles['questions__container-title']}>ОТВЕТЫ НА <span>ЧАСТЫЕ ВОПРОСЫ</span></h2>
            <div className={styles['questions__container-list']}>
                <QuestElement 
                    number="1"
                    title="Какие есть противопоказания для тренировок?"
                    description={`Данные программы тренировок подходят как новичкам, так и для тех, кто долго занимается спортом.
Нельзя беременным, с хроническими заболеваниями в стадии обострения, гипертоникам.`}
                />
                <QuestElement 
                    number="2"
                    title="Ограничения в возрасте"
                    description={`Проект подходит девушкам старше 14 лет. В остальном возрасте ограничений нет. 
Нужно ориентироваться на собственное самочувствие.`}
                />
                <QuestElement 
                    large={true}
                    number="3"
                    title="Нужен ли инвентарь?"
                    description={`Есть курс без использования инвентаря “Тренировки дома - “новичок”.

Во всех остальных курсах необходим инвентарь:
1. Фитнес-ленты;
2. Гантели: 3 или 4кг. для рук и плеч, а для ягодиц 7кг. и более.

У кого нет ничего дома для тренировок и нет возможности что-то
заказать, то можно использовать:
1. Бутылки 1 или 5л.
2. 2 шт. по 1,5л.
3. 2 шт. по 0,5л.`}
                />
                <QuestElement 
                    number="4"
                    title="Формат тренировок"
                    description={`Тренировки в записи Вы выполняете в удобное для Вас время, в своем ритме.
Время тренировки 30 минут - 1 час.`}
                />
                <QuestElement 
                    number="5"
                    title="Рацион"
                    description={`Рацион в формате конструктора.
Калорийность блюд разная, Вы подбираете под свое КБЖУ.`}
                />
                <QuestElement 
                    number="6"
                    title="Сколько кг. можно скинуть за месяц тренировок?"
                    description={`Результат индивидуален и зависит от Вашего стартового веса, метаболизма и дисциплины.
Чем Вы добросовестнее - тем круче будет результат.`}
                />
                <QuestElement 
                    number="7"
                    title="Вес не вернется после курса?"
                    description={`Если Вы продолжите питаться правильно и регулярно тренироваться, то вес точно не вернется, 
а фигура и качество тела будут становиться все лучше и лучше.
За 30 дней Вы научитесь правильно питаться и полюбите движение.`}
                />
            </div>
        </div>
    )
}