import styles from './WhoWant.module.css'
import WhoWantCard from './WhoWantCard'

export function WhoWant() {

    return (
        <div className={styles['container__content']}>
            <div className={styles['container__content-item1']}>
                <WhoWantCard 
                    text={`Улучшить качество и тонус
тела без изнурительных
тренировок и диет`} 
                    number="1"/>
                <WhoWantCard 
                    text={`Подтянуть живот
и убрать бока`}
                    number="2"/>
                <WhoWantCard 
                    text='Снизить лишний вес'
                    number="5"/>
            </div>
            <div className={styles['container__content-item2']}>
                <WhoWantCard 
                    text='Убрать рыхлость и дряблость'
                    number="3"/>
                    <h2>ПРОГРАММЫ ДЛЯ ТЕХ, <br /><span>КТО ХОЧЕТ</span></h2>
                <WhoWantCard 
                    text={`Улучшить качество кожи,
сгладить целлюлит`}
                    number="6"/>
            </div>
            <div className={styles['container__content-item3']}>
                <WhoWantCard 
                    text={`Придать телу
рельефность`} 
                    number="4"/>
                <WhoWantCard 
                    text={`Убрать лишние
жировые отложения`} 
                    number="7"/>
            </div>
        </div>
    )
}