import styles from './GetRid.module.css'
import WhoWantCart from './WhoWantCard'
import { useInView } from 'react-intersection-observer';

export function GetRid() {
    const { ref, inView } = useInView({
      threshold: 0.2,
      triggerOnce: true,
    });

    return (
        <div className={styles['getrid__container']}>
            <div className={styles['getrid__container-text']}>
                <h2>Вы не только избавитесь<br />от лишнего веса, но и...</h2>
                <p>Такой подход гарантирует, <br />что сброшенные килограммы не вернутся!</p>
            </div>
            <div ref={ref} className={`${styles['getrid__container-cards']} ${inView ? styles.visible : ""}`}>
                <WhoWantCart 
                    number={1} 
                    text={`Уменьшите объемы
в талии и бедрах`}/>
                <WhoWantCart 
                    number={2} 
                    text={`Научитесь
питаться осознанно`}/>
                <WhoWantCart 
                    number={3} 
                    text={`Повысите тонус мышц
и выносливость`}/>
                <WhoWantCart 
                    number={4} 
                    text={`Получите заряд бодрости
на весь день`}/>
            </div>
        </div>
    )
}