import styles from './ModalAuth.module.css'

export function ModalAuth() {
    return (
        <div className={styles.wrapper}>
            <div className={styles.container}>
                <h2 className={styles.paragraph}>JULIIAZHIVAEVA</h2>
                <input id='email' className={styles.input} type="text" placeholder='ПОЧТА'/>
                <input id='password' className={styles.input} type="text" placeholder='ПАРОЛЬ' />
                <div className={styles.utils}>
                    <p className={styles['utils-element']}>Регистрация</p>
                    <p className={styles['utils-element']}>Забыли пароль?</p>
                </div>
                <button className={styles.btn}>ВОЙТИ</button>
            </div>
        </div>
    )
}